import { pages } from "projectSettings"
import React from "react"
import { GatsbySeo } from "gatsby-plugin-next-seo"
import Layout from "globalLayoutsIndexPage"
import styled from "styled-components"
import HomePageCover from "projectOrganisms/HomePageCover"
import * as helpers from "globalLibs/helpers"

const PageStyle = styled.div``

export default function NotFoundPage(props) {
  return (
    <>
      <GatsbySeo
        title={pages.notFound.title}
        description={pages.notFound.description}
        canonical={props.location.href}
        openGraph={{
          url: props.location.href,
          title: pages.notFound.title,
          description: pages.notFound.description,
        }}
      />
      <Layout>
        <PageStyle
          as="article"
          className="container-fluid mt-48"
          style={{ maxWidth: "992px" }}
        >
          <h1 className="mb-16">Page not found</h1>
          <hr className="mt-0"></hr>
        </PageStyle>
      </Layout>
    </>
  )
}
